<template>
    <section id="FeaturesSection"
    :style="{
        height:container_H,
        background:`linear-gradient(${direction},${one_color},${two_color})`,
        display:dp,
        gridTemplateColumns:`${grid1} ${grid2} ${grid3}`,
        justifyItems: 'center',  
        alignItems: 'center'    
    }"
    >
        <slot></slot>
    </section>
</template>

<script>
export default{
    name: 'FeaturesSection',
    props:{
        container_H: String,
        direction: String,
        one_color: String,
        two_color: String,
        dp       : String,
        grid1    : String,
        grid2    : String,
        grid3    : String
    }
}
</script>

<style scoped>
#FeaturesSection{
    /* border: 1px solid red; */
    width: 100%;
    align-items: center;
    justify-content: center;
}
</style>