import Vue from "vue";
import VueRouter from "vue-router";
/* import Home from "@/views/Home.vue"; // 主页组件
import About from "@/views/About.vue"; // 其他页面组件 */
/* import App from "@/App.vue" */
import CardListFeatures from "@/components/featuressection/CardListFeatures.vue"
import ContributeSection from "@/components/ContributeSection.vue"
import WorksSection from "@/components/WorksSection.vue"


Vue.use(VueRouter);

const router = new VueRouter({
  mode: "hash", // 使用 HTML5 History 模式
  routes: [
    { path: "/CardListFeatures", component: CardListFeatures },
    { path: "/ContributeSection", component: ContributeSection },
    { path: "/WorksSection", component: WorksSection }
  ],
  scrollBehavior(to) {
    if (to.hash) {
      return {
        el: to.hash, 
        behavior: "smooth"
      };
    }
    return { top: 0 };
  }
});

export default router;
