<template>
    <div id="cardlistfeatures"
        class="CardListFeatures" 
        @mouseover="handelmouseover"
        @mouseleave="handlemouseleave"
        @mousemove="handlemouseMove"
        :style="{
            background: `linear-gradient(${direction}deg, ${one_color}, ${two_color})`,
            transform: `perspective(500px) rotateX(${rotateAngleX}deg) rotateY(${rotateAngleY}deg)`
        }"
        ref="cardFeatures"
    >
        <span>{{ title }}</span>
        <p>{{ textbody }}</p>
<!--         {{ mouseX }}<br>
        {{ mouseY }}<br>
        {{ direction }} -->
    </div>
</template>

<script>
export default {
    name: 'CardListFeatures',
    props: {
        title: String,
        textbody: String,
        one_color: String,
        two_color: String
    },
    data() {
        return {
            mouseX: 0,
            mouseY: 0,
            rotateAngleX: 0, // 用于存储旋转角度
            rotateAngleY: 0,
            direction: 0,
            intervalId: null
        };
    },
    mounted() {
        // 组件挂载后，检查 ref 是否正确绑定
        if (!this.$refs.cardFeatures) {
            console.error("cardFeatures ref 绑定失败！");
        }
        this.startdirectionloop();
    },
    beforeDestroy() {
        // 在组件销毁时清除定时器
        clearInterval(this.intervalId);
    },
    methods: {
        startdirectionloop() {
            this.intervalId = setInterval(() => {
                // 每次增加 direction，直到达到 360，再从 0 开始
                this.direction = (this.direction + 1) % 360;
            }, 10); // 每 16 毫秒更新一次，即 60fps
        },

        handlemouseMove(event) {
            if (!this.$refs.cardFeatures) return;
            const rect = this.$refs.cardFeatures.getBoundingClientRect();
            const x = event.clientX - rect.left;
            const y = event.clientY - rect.top;

            // 计算旋转角度时，使用一个较小的系数来缩小旋转角度
            const factor = 0.3; // 控制旋转的缩小系数

            this.rotateAngleX = ((y - rect.height / 2) / rect.height * 180) * factor;
            this.rotateAngleY = -((x - rect.width / 2) / rect.width * 180) * factor;

            // 更新鼠标坐标
            this.mouseX = x;
            this.mouseY = y;
        },
        handelmouseover() {  /* 鼠标移入 */
            /* console.log("鼠标移入"); */
        },
        handlemouseleave() { /* 鼠标移出 */
            /* console.log("鼠标移出"); */
            this.rotateAngleX = 0;
            this.rotateAngleY = 0;
        }
    }
};
</script>

<style scoped>
.CardListFeatures {
    width: 20%;
    height: 70%;
    margin: 30px;
    padding: 20px;
    max-width: 190px;
    max-height: 300px;
    min-width: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 5px;
    user-select: none;
    transform-style: preserve-3d;
    box-shadow: 0 5px 35px rgba(255, 255, 255, 0.1);
    transition: all 0.3s ease;

}
.CardListFeatures:hover{
    box-shadow: 0 5px 35px rgba(255, 255, 255, 0.2);
}
.CardListFeatures span {
    color: #333;
    font-size: 20px;
    margin-bottom: 20px;
    font-weight: 900;
}
.CardListFeatures p {
    color: #333;
    font-weight: 600;
    font-size: 15px;
}
</style>
