<template>
    <section id="contribute">
        <h2>{{ title }}</h2>
        <p>{{ textbody }}</p>
        <a :href="href"
        :style="{
            background: `linear-gradient(${direction}deg, #2af598, #009efd)`
        }"
        > {{ buttontext }} </a>


<!-- 
        
如何贡献
我欢迎任何形式的贡献！

加入项目
-->


    </section>
</template>

<script>
export default{
    name: 'ContributeSection',
    props:{
        title: String,
        textbody: String,
        buttontext: String,
        href: String
    },
    data(){
        return{
            direction:0
        };
    },
    mounted(){
        this.startdirectionloop();
    },
    beforeDestroy() {
        // 在组件销毁时清除定时器
        clearInterval(this.intervalId);
    },
    methods:{
        startdirectionloop(){
            this.intervalId = setInterval( ()=> {
                this.direction = (this.direction + 1) % 360;
            },16);
        }
    }
}
</script>

<style scoped>
#contribute{
   /*  border: 1px solid red; */
    height: 90%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    letter-spacing: 2px;
    user-select: none;
}
#contribute h2{
    margin: 20px 0;
    font-size: 30px;
}
#contribute p{
    margin-bottom: 10px;
}
#contribute a{
    text-decoration: none;
    color: #fff;
    /* border: 1px solid red; */
    padding: 15px 20px;
    border-radius: 100px;
    color: #333;
    font-weight: 900;
    transition: all 0.3s ease;
    box-shadow: 0 5px 25px rgba(255, 255, 255, 0);
    margin-top: 40px;
}
#contribute a:hover{
    box-shadow: 0 5px 25px rgba(255, 255, 255, 0.2);
}
</style>