<template>
    <div class="MainSection">
        <div>
            <h1>{{ title }}</h1>
            <p>{{ textbody }}</p>
            <a href="https://github.com/zsui2354/MasteringClassroom/releases/download/main/default.zip"
            :style="{
                background: `linear-gradient(${direction}deg, #92fe9d, #00c9ff)`
            }"
            
            
            >{{ buttonText }}
        </a>
        </div>
        <div>
            <img ref="img1" src="../assets/showcase-image1.png" alt="404">
            <img ref="img2" src="../assets/showcase-image2.png" alt="404">
        </div>
    </div>
</template>

<script>
export default{
    name: 'MainSection',
    props:{
        title: String,
        textbody: String,
        buttonText: String,
    },
    data(){
        return{
            direction:0
        };
    },
    mounted() {
        this.startdirectionloop();
    },
    beforeDestroy() {
        // 在组件销毁时清除定时器
        clearInterval(this.intervalId);
    },
    methods:{
        startdirectionloop() {
            this.intervalId = setInterval(() => {
                // 每次增加 direction，直到达到 360，再从 0 开始
                this.direction = (this.direction + 1) % 360;
            }, 10); // 每 16 毫秒更新一次，即 60fps
        },
    }
}
</script>

<style scoped>
.MainSection{
    /* background-color: rgba(23, 32, 41, 0.753); */
    /* border: 1px solid red; */
    background: linear-gradient(to right,#1e2a36,#163541);
    width: 100%;
    height: 100%;
    position: relative;
    margin-top: 60px;
    min-height: 700px;
    display: flex;
    overflow:hidden ;
}
.MainSection *{
    /* border: 1px solid red; */
}
.MainSection div:nth-child(1){
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* border: 1px solid red; */
    user-select: none;
}
.MainSection div:nth-child(1) h1{
    position: relative;
    width: 100%;
    top: -100px;
    font-size: 50px;
    background:linear-gradient(90deg , #fff, rgb(18, 105, 236),#fff);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
.MainSection div:nth-child(1) p{
    font-size: 20px;
    font-weight: 900;
    background-image: linear-gradient(0deg, #2af598 0%, #009efd 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    line-height: 50px;
}
.MainSection div:nth-child(1) a{
    position: relative;
    transition: all 0.5s ease;
    top: 100px;
    padding:20px 40px;
    border-radius: 100px;
    /* background-image: linear-gradient(to bottom, #92fe9d 0%, #00c9ff 100%); */
    text-decoration: none;
    letter-spacing: 5px;
    font-size: 20px;
    color: #333;
    font-weight: 900;
    color: #fff;
}
.MainSection div:nth-child(1) a:hover{
    box-shadow: 0 0px 35px rgba(255, 255, 255, 0.35);
    color: #333;
}
.MainSection div:nth-child(2){
    display: flex;
    height: 100%;
    width: 50%;
    transform-style: preserve-3d;
    perspective:3000px;
    /* border: 1px solid red; */
    align-items: center;
    justify-content: center;
    user-select: none;
}
.MainSection div:nth-child(2) img:nth-child(1){
    position: absolute;
    /* width: 300px; */
    width: 50%;
    cursor: pointer;
    border-radius: 7px;
    box-shadow: 0 5px 25px rgba(255, 255, 255, 0.35);
    transform: rotateY(30deg) rotateX(-30deg);
    transition: all 0.5s ease;
}
.MainSection div:nth-child(2) img:nth-child(1):hover{
    transform: translateZ(100px) scale(1);
    transform: rotateY(0deg) rotateX(0deg);
    transform: scale(1.7);
    box-shadow: 0 5px 25px rgba(255, 255, 255, 0);
}
.MainSection div:nth-child(2) img:nth-child(2){
    position: absolute;
    left: 50%;
    width: 50%;
    cursor: pointer;
    border-radius: 7px;
    box-shadow: 0 5px 25px rgba(255, 255, 255, 0.35);
    transform: rotateY(30deg) rotateX(-35deg);
    transition: all 0.5s ease;
    transform: translateZ(-190px) rotateY(30deg) rotateX(-35deg);
}
.MainSection div:nth-child(2) img:nth-child(2):hover{
    transform: translateZ(300px) rotateY(0deg) rotateX(0deg) scale(2.5);
    box-shadow: 0 5px 25px rgba(255, 255, 255, 0);
}

</style>